import React from 'react';
import Container from 'react-bootstrap/Container';
import Badge from 'react-bootstrap/Badge';
import { HeartIcon, HeartFillIcon } from './Icons';

function RecordPane(props) {
    const [getRefresh, setRefresh] = React.useState(0);

    const clickFav = (o) => {
        if (props.UpdateFavorite) {
            props.UpdateFavorite(props.RecIndex);
        }
        setRefresh(getRefresh + 1);
    }

    const IsFavorite = (n) => {
        return props.Favorite[n];
    }

    let funcLevel = (l) => {
        switch (l) {
            case '1':
                return "danger";
            case '2':
                return "danger";
            case '3':
                return "secondary";
            case '4':
            default:
                return "secondary";
        }
    }

    let rec = props.Index.Records[props.RecIndex];

    return (
        <Container fluid="xs" className="recordPane mb-3">
            <div>
                <small>
                    <Badge className="mr-1" pill={true} bg={funcLevel(rec.Level)}>{rec.Level}</Badge>
                    <Badge className="mr-1" bg="primary">{rec.Date}</Badge>
                    <Badge className="mr-1" bg="primary">{rec.Time}</Badge>
                    <Badge className="mr-1" bg="info">Pid:{rec.Pid}</Badge>
                    <Badge className="mr-1" bg="info">Tid:{rec.Tid}</Badge>
                    <Badge className="mr-1" bg="success">Stream:{rec.Stream}</Badge>
                    <Badge className="mr-1" bg="success">ErrorType:{rec.ErrorType}</Badge>
                    <Badge className="mr-1" bg="secondary">{rec.Function} {rec.File}:{rec.Line}</Badge>
                    {
                        rec.MDResult > 0
                            ? <Badge className="mr-1" bg="warning">MDResult:{rec.MDResult}</Badge>
                            : null
                    }
                    {
                        rec.ErrorCode > 0
                            ? <Badge className="mr-1" bg="warning">ErrorCode:{rec.ErrorCode}</Badge>
                            : null
                    }
                    {
                        rec.ErrorCodeMsg.length > 0
                            ? <Badge className="mr-1" bg="warning">ErrorCodeMsg:{rec.ErrorCodeMsg}</Badge>
                            : null
                    }
                    {
                        rec.Instance && Number(rec.Instance) > 0
                            ? <Badge className="mr-1" bg="secondary">Inst:{rec.Instance}</Badge>
                            : null
                    }
                    {
                        IsFavorite(props.RecIndex)
                            ? <span onClick={clickFav}><HeartFillIcon /></span>
                            : <span onClick={clickFav}><HeartIcon /></span>
                    }
                </small>
            </div>
            {rec.Message}
        </Container>);
}

export default RecordPane;
